/* eslint-disable */
import React, { Component } from 'react'
import axios from 'axios'

import './player.less';

import { Row, Col, message } from 'antd';
import { withRouter } from 'react-router-dom'

class player extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isP1Disabled: true,
      isP2Disabled: true,
      valueP1: '',
      valueP2: '',
      isP1Display: true,
      isP2Display: true,
      P1: null,
      P2: null
    }
  }

  /* 输入时触发的 onChange事件 */
  // P1
  changeP1Evt(e) {
    this.setState({
      valueP1: e.target.value
    })
    if (e.target.value !== '') {
      this.setState({
        isP1Disabled: false
      })
    } else {
      this.setState({
        isP1Disabled: true
      })
    }
  }

  // P2
  changeP2Evt(e) {
    this.setState({
      valueP2: e.target.value
    })
    if (e.target.value !== '') {
      this.setState({
        isP2Disabled: false
      })
    } else {
      this.setState({
        isP2Disabled: true
      })
    }
  }

  /* 点击btn-submit时的事件 */
  async subP1Evt(e) {
    const warning = () => {
      message.warning('非法字段，请重新输入');
    };
    // 阻止默认跳转事件
    e.preventDefault()
    if (this.state.valueP1.trim() === '') {
      warning()
      this.setState({
        valueP1: '',
        isP1Disabled: true,
      })
    } else {
      const res = await axios({
        method: 'GET',
        url: 'https://api.github.com/users/'.concat(this.state.valueP1.trim())
      })
      const player1 = res.data
      sessionStorage.setItem("p1", JSON.stringify({ player1 }))
      this.setState({
        isP1Display: false,
        P1: player1
      })
    }
  }

  async subP2Evt(e) {
    const warning = () => {
      message.warning('非法字段，请重新输入');
    };
    // 阻止默认跳转事件
    e.preventDefault()
    if (this.state.valueP2.trim() === '') {
      warning()
      this.setState({
        valueP2: '',
        isP2Disabled: true,
      })
    } else {
      const res = await axios({
        method: 'GET',
        url: 'https://api.github.com/users/'.concat(this.state.valueP2.trim())
      })
      const player2 = res.data
      sessionStorage.setItem("p2", JSON.stringify({ player2 }))
      this.setState({
        isP2Display: false,
        P2: player2
      })
    }
  }

  /* 点击btn-close时的事件 */
  CloseP1Evt(e) {
    e.preventDefault()
    sessionStorage.removeItem("p1")
    this.setState({
      valueP1: '',
      isP1Disabled: true,
      isP1Display: true
    })
  }

  CloseP2Evt(e) {
    e.preventDefault()
    sessionStorage.removeItem("p2")
    this.setState({
      valueP2: '',
      isP2Disabled: true,
      isP2Display: true
    })
  }

  /* 点击btn-battle */
  battleEvt() {
    this.props.history.push(`/battle/result?player1=${this.state.P1.login}&player2=${this.state.P2.login}`)
  }

  render() {
    const { isP1Disabled, isP2Disabled, valueP1, valueP2, isP1Display, isP2Display } = this.state
    return (
      <div>
        <div className="player">
          <Row>
            <Col xs={{ span: 24 }} lg={{ span: 24 }}><p>Players</p></Col>
          </Row>
          <Row>
            <ul className="player-ul">
              <Col lg={{ span: 6, offset: 3 }} xs={{ span: 24 }}>
                {
                  isP1Display 
                  ? 
                  (
                    <li className="select-P1">
                      <h3>Player One</h3>
                      <form>
                        <input type="text" placeholder="github username" value={valueP1} onChange={this.changeP1Evt.bind(this)} name="player1" />
                        <button type="button" className="btn-submit" disabled={isP1Disabled} onClick={this.subP1Evt.bind(this)}>submit</button>
                      </form>
                    </li>
                  )
                  : 
                  (
                    <li className="display-P1">
                      <figure>
                        <img src={this.state.P1.avatar_url} alt='heade img' />
                        <figcaption>{this.state.P1.login}</figcaption>
                      </figure>
                      <button type="button" className="btn-close" onClick={this.CloseP1Evt.bind(this)}>X</button>
                    </li>
                  )
                }
              </Col>
              <Col lg={{ span: 6, offset: 6 }} xs={{ span: 24 }}>
                {
                  isP2Display ? (
                    <li className="select-P2">
                      <h3>Player Two</h3>
                      <form>
                        <input type="text" placeholder="github username" value={valueP2} onChange={this.changeP2Evt.bind(this)} name="player2" />
                        <button type="button" className="btn-submit" disabled={isP2Disabled} onClick={this.subP2Evt.bind(this)}>submit</button>
                      </form>
                    </li>
                  )
                    : (
                      <li className="display-P2">
                        <figure>
                          <img src={this.state.P2.avatar_url} alt='heade img' />
                          <figcaption>{this.state.P2.login}</figcaption>
                        </figure>
                        <button type="button" className="btn-close" onClick={this.CloseP2Evt.bind(this)}>X</button>
                      </li>
                    )
                }
              </Col>
            </ul>
          </Row>
          {
            (isP1Display === false && isP2Display === false) ? (
              <Row justify="center">
                <Col xs={{ span: 2 }} lg={{ span: 2 }}>
                  <button type="button" className="btn-battle" onClick={this.battleEvt.bind(this)}>Battle</button>
                </Col>
              </Row>
            )
              :
              ''
          }
        </div>
      </div>
    )
  }
}
export default withRouter(player)
